.log {
    font-family: Source Han Sans CN;

    .card-top {
        width: 100%;
        display: flex;
        justify-content: space-between;
        margin-top: 0.26rem;

        .title {
            display: flex;
            flex-direction: row;
            font-size: 0.18rem;
            margin-left: 0.30rem;
            color: #9696A3;

            .title-l {
                height: 0.18rem;
                font-weight: 400;
                cursor: pointer;
            }

            .title-r {
                height: 0.18rem;
                margin-left: 0.4rem;
                cursor: pointer;
            }
        }

        .top-search-input {
            width: 2.30rem;
            height: 0.4rem;
            margin-right: 0.4rem;
            border-radius: 4px;
            border: 1px solid #D9DBE1;
        }
    }

    .card-main {
        margin-top: 0.18rem;


        
        .ant-table-thead>tr>th {
            background: #F2F2FD;
            color: #9696A3;
            font-size: 0.13rem;
            line-height: 1;
            padding: .13rem 0 .13rem .3rem;
        }

        .ant-table-cell-row-hover{
            background: #F0F0FD !important;
        }

        // .ant-table-thead>tr>th {
        //     background: #F2F2FD;
        //     color: #9696A3;
        //     height: 0.13rem;
        //     font-size: 0.13rem;
        //     font-weight: 400;
        // }

        .ant-table-tbody>tr>td {
            padding: 0 0 0 0.3rem;
            height: 0.6rem;
            font-size: 0.14rem;
        }

        .ant-table-cell::before {
            height: 0px !important;
        }

        .pay-link {
            width: 0.42rem;
            height: 0.14rem;
            font-size: 0.14rem;
            font-weight: 400;
            line-height: 0px;
            color: #5C5EE4;
            opacity: 1;
        }
    }


    .product_names {
        position: relative;
        height: 0.14rem;

        .product-type {
            display: inline;
            padding: 0 3px;
            height: 0.20rem;
            position: absolute;
            top: 0;
            left: 0;
            transform: translateY(calc(-100% - 0.06rem)) translateX(calc(-100% + 0.16rem));
            background: #FFFFFF;
            box-shadow: 0px 0.06rem 0.12rem rgba(30, 52, 90, 0.1200);
        }

        .product-text {
            height: 0.14rem;
            font-size: 0.14rem;
            font-weight: 400;
            line-height: 0px;
            color: #040E34;
            opacity: 1;

            img {
                margin-right: 0.08rem;
            }
        }
    }



    .ant-table-thead>tr>th {
        background: #F2F2FD;
        color: #9696A3;
        height: 0.13rem;
        font-size: 0.13rem;
        font-weight: 400;
    }

    .ant-table-cell::before {
        height: 0px !important;
    }

}
@primary-color: #5C5EE4;